require('./vender');

import Vue from 'vue';
import VueRouter from 'vue-router';
import App from './app.vue';

// Routes & Vuex
import { routes } from './routes';
import store from './vuex/store';

// è¼‰å…¥ iview
import iView from 'iview';

// è¼‰å…¥ App scss
import '../sass/app.scss';

const router = new VueRouter({
  routes,
//   mode: 'history',
  linkActiveClass: 'active',
  linkExactActiveClass: 'active',
});

router.beforeEach( (to, form, next) => {
    const loginUserData = JSON.parse(localStorage.getItem('token'));

    // if (!isLogin) {
    // }
    if(loginUserData) {
        // å¦‚åœ¨ç™»å…¥ç‹€æ…‹ä¸‹é‡æ•´ï¼Œæ‡‰è©²æŠŠå­˜åœ¨localStorageçš„è³‡æ–™å†é€çµ¦stateä¸€æ¬¡
        // å¦‚ä¸²å¾Œç«¯ï¼ŒAPIæ‡‰è©²å†å‚³çµ¦state
        store.commit({
            type: 'setUserData',
            userData: loginUserData,
        });
        next();
    } else {
        if ( to.path !== '/login' && to.path !== '/signup' && to.path !== '/search_password') {            
            next('/login');
        }
        else {            
            next();
        }
    }
});

Vue.use(iView);

new Vue({
  el: '#page-wrapper',
  store,
  router,
  render: h => h(App)
});


