
<template>
  <section class="login">
    <h1>SignUp Page</h1>
    <form @submit.prevent="checkForm"
          novalidate="true"
          >
      <div class="input__wrap">
        <!-- <label>ä¿¡ç®±</label> -->
        <input type="email" 
               v-model="email.value"
               @focus="clearEmail($event)"
               @blur="check($event)"
               placeholder="ä¿¡ç®±"
               >
        <p v-show="email.errorMsg.length > 0"
           class="input__status--error js__input__status--error">{{email.errorMsg}}</p>
      </div>
      <!-- <div class="input__wrap">
        <input type="text" v-model="account" placeholder="å¸³è™Ÿ" required>
      </div> -->
      <div class="input__wrap">
        <input type="password"
               v-model="passWord.value"
               @focus="clearPassWord($event)"
               @blur="check($event)"
               placeholder="å¯†ç¢¼"
               >
        <p v-show="passWord.errorMsg.length > 0"
           class="input__status--error js__input__status--error">{{passWord.errorMsg}}</p>
      </div>
      <div class="form_buttonbar">
        <button type="submit"
                class="primary"
                >Sign Up</button>
      </div>
    </form>
  </section>
</template>
<script>
import { checkInputV } from '../utils/form';
import { mapGetters } from 'vuex'

  export default {
    data () {
      return {
        inputHasError: false,
        errors: [],
        email: {
          value: '',
          errorMsg: '',
        },
        passWord: {
          value: '',
          errorMsg: '',
        },
      }
    },
    computed: {
      ...mapGetters([
        'errorState'
      ])
    },
    methods: {
      // **
      // * Can Use vee-validate!
      // *
      clearEmail(e) {
        this.email.errorMsg = '';
      },
      clearPassWord(e) {
        this.passWord.errorMsg = '';
      },
      check(e) {
        // checkInput will return [type, isError, errorMsg]
        let errorStaus = checkInput(e.target, e.target.value),
            type       = errorStaus[0],
            isError    = errorStaus[1],
            errorMsg   = errorStaus[2];

        // æŠŠ'æ˜¯å¦æœ‰éŒ¯èª¤'ç•¶ä½œé–‹é—œ
        this.inputHasError  = isError;

        if (type == 'email') {
          this.email.errorMsg = isError ? errorMsg : '';
        }
        if (type == 'password') {
          this.passWord.errorMsg = isError ? errorMsg : '';
        }
      },
      checkForm(e) {
        this.errors = [];
        // **
        // * é€£ç·šéŒ¯èª¤ï¼Ÿ
        // * è³‡æ–™éŒ¯èª¤ï¼Ÿ å¸³è™Ÿé‡è¤‡ï¼Ÿ
        // * è¨»å†ŠæˆåŠŸï¼Œç›´æŽ¥ç™»å…¥
        // *

        // å¦‚æžœä»»ä¸€inputæœ‰éŒ¯èª¤
        if(this.inputHasError) {
          return;
        }
        if(!this.email.value) {
          // è¨ˆç®—æ˜¯å¦æœ‰éŒ¯èª¤
          // errorState å­˜åœ¨ Vuex sotre.app
          this.errors.push( this.errorState.emailempty );
          
          this.email.errorMsg = this.errorState.emailempty;
        }
        if(!this.passWord.value) {
          this.errors.push( this.errorState.passwordempty );

          this.passWord.errorMsg = this.errorState.passwordempty;
        }
        if (!this.errors.length) {
          alert('è¨»å†ŠæˆåŠŸ');
          // ** 
          // * å¾Œç«¯APIä¸²æŽ¥
          // * å¦‚æžœæœ‰å…¶ä»–éŒ¯èª¤è¨Šæ¯ï¼Œå¯ä»¥ç”±æˆ‘é€™é‚Šé–‹ä¸€å€‹ä½ç½®ï¼ŒæŠŠstore è£¡çš„ stateå«å‡ºä¾†ç”¨
          // * è®“å‰ç«¯æŽ§åˆ¶éŒ¯èª¤è¨Šæ¯
          // *
          // axios.get(apiUrl)
          // .then(res => res.json())
          // .then(res => {
          //   if (res.error) {
          //     this.errors.push(res.error);
          //   } else {
          //     alert('ok!');
          //   }
          // });
        }
        else {
          alert('è¨»å†Šå¤±æ•—');
        }
      }
    }
  }
</script>


